$price-color:#b12603;
$border: 1px #ccc solid;
$border-dog:1px #000 solid;
$border-dotted: 1px #ccc dotted;

#DC{
    display: flex;
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0px;
    left: 0px;
    z-index: 1001;
    &>div{
        flex: 1 1 0;
        position: relative;
        
        button{
            width: 100%;
            height:100%;
            background: url("../public/img/dog.png") no-repeat left bottom;
            background-size: contain;
            filter: brightness(50%);
            &:hover{
                filter: none;
            }
            &:hover div{
                transform: translateX(-50%) rotate(25deg);
            }
            div{
                font-size: 1.875rem;
                width: 400px;
                height: 200px;
                cursor: pointer;
                position: absolute;
                transform: translateX(-50%) rotate(-25deg);
                left: 50%;
                background-size: contain;
                font-weight: bold;
            }
            .dogtext{
                top:100px;
                background: url("../public/img/DCdog.png") no-repeat;
                line-height: 200px;
                text-align: center;
            }
        }
        .catimg{
            background-image: url("../public/img/cat.png");
            div{
                text-align: left;
                padding-left: 50px;
                line-height: 220px;
                bottom: 100px;
                color: #ffc0cb;
                background: url("../public/img/DCcat.png") no-repeat;
            }
        }
    }
    .dog{
        background: #ededed;
    }
    .cat{
        background: #888;
    }
    .m_logo{
        width: 150px;
        height: 150px;
        position: absolute;
        transform: translateX(-50%);
        top: 10px;
        left: 50%;
        background: url("../public/img/logo.png") no-repeat;
        background-size: contain;
        z-index: 1;
    }
}
#main_header{
    width: 100%;
    height: 100px;
    position: fixed;
    background: #fff;
    z-index: 1000;
    h1{ 
        font-size: 3.125rem;
        cursor: pointer;
        width: 300px;
        margin: 0 auto;
        height: 100px;
        line-height: 100px;
        text-align: center;
        position: relative;
        &::before{
            content: "";
            display: block;
            width: 80px;
            height: 100%;
            background: url("../public/img/logo.png") no-repeat center;
            background-size: contain;
            position: absolute;
            left: 0px;
            top: 0px;
        }
    }
    .cart{
        width: 50px;
        height: 50px;
        background: url("../public/img/cart.png") no-repeat;
        background-size: contain;
        position: absolute;
        right: 200px;
        top: 25px;
        cursor: pointer;
    }
    .gnb{
        border-top: $border;
        border-bottom: $border;
        padding: 0px 50px;
        height: 50px;
        line-height: 50px;
        display: flex;
        justify-content: center;
        background: #fff;
        &>li:first-of-type{
            display: block;
            background: #fff;
            border: $border-dog;
        }
        &>li:nth-of-type(5){
            background: #555;
            a{
                color: #ffc0cb;
            }
        }
        a{  
            padding: 0px 30px;
            display: block;
            color: #333;
        }
        li:nth-of-type(2){
            &:hover .dog1{display: flex;}
        }
        li:nth-of-type(3){
            &:hover .dog2{display: flex;}
        }
        li:nth-of-type(4){
            &:hover .dog3{display: flex;}
        }
        li:nth-of-type(6){
            &:hover .cat1{display: flex;}
        }
        li:nth-of-type(7){
            &:hover .cat2{display: flex;}
        }
        li:nth-of-type(8){
            &:hover .cat3{display: flex;}
        }
        li:nth-of-type(6),li:nth-of-type(7),li:nth-of-type(8){
            .sub{
                background: #555;
                .nav_img{
                    background-image: url("../public/img/cat.png");
                }
                a{
                    color:#fff;
                    &:hover{
                        color: #ffc0cb;
                    }
                }
            }
        }
        .sub{
            width: 100%;
            height: 300px;
            position: absolute;
            top: 151px;
            left: 0px;
            padding: 0px 100px;
            box-sizing: border-box;
            box-shadow: 0px 1px 4px 2px rgba($color: #000000, $alpha: 0.3);
            border-top: $border;
            border-bottom: $border;
            display: none;
            background: #fff;
            z-index: -1;
            .nav_txt{
                width: 20%;
                background: url("../public/img/logo.png") no-repeat center;
                background-size: contain;
            }
            ul{
                width: 50%;
                padding-left: 50px;
                a{
                    display: block;
                    width: 100%;
                    height: 100%;
                    cursor: pointer;
                    color: #555;
                    &:hover{
                        color: #000;
                    }
                }
            }
            .nav_img{
                width: 30%;
                background: url("../public/img/dog.png") no-repeat;
                background-size: contain;
            }
        }
    }
}
#main_banner{
    padding-top: 200px;
    height: 500px;
    .slick-slider{
        width: 100%;
        height: 100%;
        background: #ccc;
        .slick-list{
            width: 100%;
            height: 100%;
            .slick-slide{
                height: 500px;
                div{
                    height: 500px;
                    img{
                        width: 100%;
                        height: 100%;
                    }
                }
            }
        }
        .slick-dots{
            bottom: 20px;
            li button:before{
                font-size: 1rem;
                color: #333;
            }
            li.slick-active button:before{
                opacity: 1;
                color: #555;
            }
        }
    }
}
#main_footer{
    width:100%;
    height: 500px;
    margin-top: 50px;
    border-top: $border;
    .footer_top{
        height: 150px;
        padding: 50px 100px;
        border-bottom: $border;
        display: flex;
        &>div{
            flex: 1 1 0;
            border-left: $border;
            padding-left: 50px;
        }
        &>div:first-of-type{ border-left: 0px; }
        .parcel{
            button{
                width: 200px;
                height: 30px;
                line-height: 28px;
                border: $border;
                border-radius: 5px;
                background: #fff;
            }
        }
        h2{ margin-bottom: 10px; }
        p{ margin-bottom: 10px; }
    }
    .footer_bottom{
        height: 100px;
        padding: 50px 100px;
        li{
            display: flex;
            margin-bottom: 10px;
        }
        span{
            padding:0px 20px;
            color: #777;
        }
        p:first-of-type>span{
            padding-left: 0px;
        }
        p:nth-of-type(2)>span::before{
            content: "|";
            padding-right: 20px;
            color: #000;
        }
    }
    .copyright{
        width: 100%;
        height: 50px;
        line-height: 50px;
        background: #000;
        color: #fff;
        text-align: center;
    }
}
#main{
    margin-top: 50px;
    h2{ 
        font-size: 3.125rem;
        text-align: center;
        margin-bottom: 50px;
    }
    .tab_box>ul{
        display: flex;
        justify-content: center;
        li{ 
            text-align: center;
            width: 150px;
            height: 50px;
            line-height: 50px;
            border: $border;
            button{
                width: 100%;
                height: 100%;
                background: #ededed;
                color: #888;
                font-size: 1rem;
                &.active{
                    background: #fff;
                    color: #000;
                }
                &.activecat{
                    background: #555;
                    color: #ffc0cb;
                }
            }
        }
        li:nth-of-type(2){
            border-left: 0px;
            border-right: 0px;
        }
    }
    .best_box{
        margin-top: 50px;
        &>div>ul{
            display: grid;
            grid-template-columns: 1fr 1fr 1fr 1fr;
            gap: 50px;
            row-gap: 50px;
            img{
                border: $border;
                width: 100%;
                height: 300px;
                background: #000;
            }
            h3{
                color: #222;
                font-size: 1rem;
                padding: 10px 0px;
                border-bottom: $border;
            }
            p{  
                font-size: 0.938rem;
                margin-top: 10px;
                color: $price-color;
                font-weight: bold;
            }
        }
        button{
            width: 200px;
            height: 50px;
            background: #fff;
            border: $border-dog;
            border-radius:3px;
            margin: 0 auto;
            margin-top: 50px;
            color: #000;
            &.maincat{
                background: #555;
                color: #ffc0cb;
                border: 0px;
            }
        }
    }
}
#sub_banner{
    margin-top: 100px;
    height: 300px;
    .sub_banner{
        width: 100%;
        height: 100%;
        background: #ccc;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}
#review{
    .slick-track{
        height: 500px;
        &>div{
            height: 100%;
        }
    }
    .slick-slider{
        margin: 0 auto;
        margin-top: 100px;
        width: 90%;
        background: #ccc;
        .slick-prev:before, .slick-next:before{
            font-size: 3.125rem;
            opacity: 1;
            color: #000;
        }
        .slick-next{
            right: -30px;
        }
        .slick-prev{
            z-index: 100;
            left: -60px;
        }
        .slick-dots li.slick-active button:before{
            opacity: 1;
        }
        .review{
            display: flex;
            height: 500px;
            background: #fff;
            justify-content: space-around;
            li{
                width: 30%;
                border: 2px #ccc solid;
                border-radius: 15px;
                img{
                    padding-top: 50px;
                    margin: 0 auto;
                    height: 40%;
                }
                span{
                    padding-top: 50px;
                    display: block;
                    height: 10%;
                    background: url("../public/img/reivew.jpg") no-repeat center;
                }
                h3{ 
                    padding: 0px 0px 20px 20px;
                    &::before{
                        content: "ID :";
                        padding-right: 10px;
                    }
                }
                p{
                    padding: 0px 20px 0px 20px;
                    font-size: 0.813rem;
                    word-break: keep-all;
                    &:nth-of-type(1){
                        text-align: center;
                        color: #555;
                    }
                }
            }
        }
    }
}
#sub_page{
    padding-top: 200px;
    &>h2{
        font-size: 2.5rem;
        text-align: center;
        padding-bottom: 50px;
        border-bottom: $border-dotted;
    }
    .FoodList{
        &>ul{
            margin-top: 50px;
            padding-bottom: 50px;
            display: flex;
            justify-content: center;
            border-bottom: $border-dotted;
            li{
                height: 50px;
                button{
                    padding: 0px 30px;
                    border-bottom: $border;
                }
            }
        }
    }
    .FoodSolt{
        &>ul{
            margin-top: 50px;
            display: flex;
            justify-content: flex-end;
            border-bottom: $border;
            li{
                height: 30px;
                button{
                    padding: 0px 15px;
                }
            }
        }
    }
    button{
        width: 100%;
        height: 100%;
        border-top: $border;
        border-left: $border;
        background: #ededed;
        color: #888;
        &:last-child{border-right: $border;}
        &.list{
            background: #fff;
            color: #000;
        }
        &.clist{
            background: #555;
            color: #ffc0cb;
        }
        &.Solt{
            background: #fff;
            color: #000;
        }
        &.cSolt{
            background: #555;
            color: #ffc0cb;
        }
    }
    .Food{
        margin-top: 50px;
        ul{
            display: grid;
            grid-template-columns: 1fr 1fr 1fr 1fr;
            gap: 50px;
            row-gap: 50px;
            padding-bottom: 50px;
            border-bottom: $border;
            li{
                width: 100%;
                height: 400px;
            }
            figure{
                width: 100%;
                height: 100%;
            }
            img{
                width: 100%;
                height: 80%;
                border: $border;
                box-sizing: border-box;
            }
            figcaption{
                width: 100%;
                height: 20%;
                h3,p{
                    font-size: 1rem;
                    height: 50%;
                    line-height: 40px;
                }
                p{  
                    font-weight: bold;
                    color: $price-color;
                    border-top: $border;
                }
            }
        }
    }
    .listNumber{
        margin-top: 25px;
        ol{
            &::before{
                content: "<";
                color: #555;
                line-height: 48px;
                padding-right: 15px;
            }
            &::after{
                content: ">";
                color: #555;
                line-height: 48px;
                padding-left: 15px;
            }
            display: flex;
            justify-content: center;
            li{
                margin: 0px 5px;
            }
            button{ 
                color: #888;
                border: $border;
                background: #ededed;
                border-radius: 3px;
                padding: 15px;
                &.List{
                    color: #000;
                    background: #fff;
                }
                &.cList{
                    color: #ffc0cb;
                    background: #555;
                }
            }
        }
    }
}
#detail{
    padding-top: 200px;
    .product_box{
        width: 100%;
        height: 500px;
        display: flex;
        justify-content: space-between;
        .product_img{
            width: 45%;
            height: 100%;
            border: $border;
            position: relative;
            img{
                position: absolute;
                transform: translate(-50%,-50%);
                left: 50%;
                top:50%
            }
        }
        .product{
            width: 45%;
            height: 100%;
            position: relative;
            h2{ 
                font-size: 1.875rem;
                padding-left: 20px;
                margin-bottom: 50px;
            }
            table{
                width: 100%;
                th{
                    width: 20%;
                    height: 50px;
                    font-size: 1.25rem;
                    color: #333;
                }
                td{
                    font-size: 1.25rem;
                    width: 80%;
                    height: 50px;
                    color: #777;
                }
                .input{
                    td{
                        display: flex;
                        align-items: center;
                    }
                    span{
                        width: 30px;
                    }
                    input{
                        width: 20px;
                        padding-left: 10px;
                        margin-right: 10px;
                    }
                    button{
                        width: 30px;
                        height: 30px;
                        border: $border;
                        background: #fff;
                        margin-right: 10px;
                        border-radius: 3px;
                    }
                }
            }
            h3{
                font-size: 2.5rem;
                padding-top: 80px;
                width: 100%;
                height: 100px;
                line-height: 100px;
                text-align: center;
                color: $price-color;
                span{
                    margin-right: 10px;
                    color: #333;
                    &::after{
                        content: ":";
                        padding-left: 10px;
                    }
                }
            }
            &>button{
                width: 200px;
                height: 50px;
                position: absolute;
                transform: translateX(-50%);
                left: 50%;
                bottom: 0px;
                background: #555;
                border-radius: 3px;
                color: #fff;
            }
        }
    }
    .detail_box{
        width: 100%;
        height: 500px;
        margin-top: 100px;
        ul{
            display: flex;
            justify-content: center;
            li{
                padding: 0px 50px;
                height: 50px;
                line-height: 50px;
                border: $border;
                &::after{
                    content: "";
                    display: block;
                    position: absolute;
                    z-index: -1;
                    left: 0px;
                    width: 100%;
                    border-bottom: 1px #333 solid;
                }
            }
        }
    }
}
#Cart{
    padding-top: 200px;
    h2{
        text-align: center;
        padding-bottom: 50px;
        border-bottom: $border;
    }
    table{
        margin-top: 50px;
        width: 100%;
        border: $border;
        border-bottom: 0;
        thead{
            th{ width: 5%;
                border-right: $border;
                height: 50px;
            }
            th:nth-of-type(2){
                width: 15%;
            }
            th:nth-of-type(3){
                width: 40%;
            }
            th:nth-of-type(4){
                width: 15%;
            }
            th:nth-of-type(5){
                width: 15%;
                border-right: 0px;
            }
            border-bottom: $border;
        }
        tbody{
            text-align: center;
            img{
                height: 80%;
            }
            tr{
                border-bottom: $border;
            }
            td{
                border-left: $border;
                height: 150px;
            }
            td:first-of-type{
                button{
                    width: 30px;
                    height: 30px;
                    margin: 0 auto;
                }
            }
            td:nth-of-type(4){
                display: flex;
                justify-content: center;
                align-items: center;
                span{
                    display: block;
                    width: 30px;
                    height: 30px;
                    line-height: 30px;
                    margin: 0px 10px;
                }
                button{
                    width: 30px;
                    height: 30px;
                    border-radius: 5px;
                }
            }
        }
    }
    .remove{
        margin: 25px 0px;
        padding-top: 25px;
        border-top: $border;
        display: flex;
        position: relative;
        button{
            width: 100px;
            height: 30px;
            margin-right: 10px;
            font-size: 0.813rem;
            border: $border;
            border-radius: 2px;
            background: #ededed;
        }
        h3{
            position: absolute;
            right: 0px;
        }
    }
    .jumon_box{
        padding-top: 50px;
        border-top: $border;
        width:100%;
        height: 50px;
        .jumon{
            width: 200px;
            height: 50px;
            position: absolute;
            transform: translateX(-50%);
            left: 50%;
            background: #555;
            color: #fff;
            border-radius: 3px;
        }
    }
}
